<template>
  <form class="mt-3" @submit.prevent="OnSubmit"> <!-- @submit.prevent - SPA -->
    <div class="form-row align-items-center">
      <div class="col-5">
        <label class="sr-only" for="inlineFormInput">Name</label>
        <input type="text" class="form-control mb-2" id="inlineFormInput" placeholder="Enter City" v-model="title">  <!-- !!!  -->
      </div>

      <div class="col-auto">
        <button type="submit" class="btn btn-primary mb-2">Покаж погоду</button>
      </div>
    </div>
  </form>
</template>

<script>
export default {
  data() {
    return {
      title: ''    //model
    }
  },
  methods: {
    OnSubmit() {
      const cityName = this.title
      this.$emit('show-city',cityName)  // push event
    }
  }
}
</script>