<template>
  <div> <!-- id="app" не нужен -->
    <!-- Image and text -->
    <nav class="navbar navbar-light bg-light">
      <router-link class="navbar-brand" to="/">
        The Weather
      </router-link>
    </nav>

    <GetCity
        @show-city="getInfo"
    />   <!-- listen for event -->

    <!-- <button type="button" class="btn btn-outline-success" @click="getInfo()">Заебись</button> -->

    <div v-if="info" class="card" style="width: 18rem;">
      <img class="card-img-top"  alt="типа картинка города">
      <div class="card-body">
        <!-- <p class="card-text">Температура: {{info.main.temp}} C</p> -->
        <p class="card-text">info: {{info}} </p>
      </div>
    </div>

  </div>
</template>

<script>
import axios from 'axios'
import GetCity from '@/components/GetCity'

export default {
  name: 'App',
  components: {
    GetCity,
  },
  data() {
    return {
      info: null
    };
  },
  methods: {
    getInfo(city) {
      console.log(city);
      //let prom = axios.get('http://api.openweathermap.org/data/2.5/weather?q=Krasnodar&units=metric&appid=e59b7ae10d8ecd6bac997c53bb907479')
      //let prom = axios.get('http://api.openweathermap.org/data/2.5/weather?q='+city+'&units=metric&appid=e59b7ae10d8ecd6bac997c53bb907479')
      let prom = axios.get('https://pmpulecture.herokuapp.com/get');
      prom.then(response => {
        this.info = response.data
      })
          .catch(error => {
            console.log(error);
          })
    },
    showCity() {
      if (!this.info) return

    }
  }
}

</script>